import { beforeEach, describe, expect, test } from '@jest/globals'
import { screen } from '@testing-library/react-native'

import { toJSON } from '../utils/customRender'

import { Text } from '@components'

import type { TextProps } from '@components'

const demoText: string = 'Demo Text'
const demoValues: TextProps = {
  text: 'This is not really a bird nest.',
}

describe('Text component', () => {
  beforeEach(() => {
    toJSON(<Text {...demoValues}>{demoText}</Text>)
  })
  test('Render component', () => {
    expect(screen).toMatchSnapshot()
  })
  test('Visiblity & text value by Role', () => {
    const input = screen.getByText(demoValues.accessibilityLabel as string)
    expect(input).toBeOnTheScreen()
    expect(input).toBeVisible()
    // expect(input.props.children).toBe(demoText)
  })
})
